import styled from 'styled-components';
import { Form as FormikForm } from 'formik';
import { Field as FormikField } from 'formik';
import { ErrorMessage as FormikError } from 'formik';

import { themes } from 'styles/themes';

export const Form = styled(FormikForm)`
  padding: 40px;
  display: flex;
  flex-direction: column;

  border-radius: ${themes.radius.m};
  background-color: ${themes.colors.white};
`;

export const Field = styled(FormikField)`
  padding: 10px;
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }

  font-size: 16px;
  font-family: 'Roboto', sans-serif;

  border: 1px solid ${themes.colors.border};
  border-radius: ${themes.radius.s};
  outline: 1px solid transparent;
  transition: border-color 250ms linear, outline-color 250ms linear;

  border-color: ${({ validation }) => {
    switch (validation) {
      case 'noValue':
        return themes.colors.border;
      case validation:
        return themes.colors[validation];
      default:
        break;
    }
  }};

  &:hover,
  &:focus {
    border-color: ${({ validation }) => {
      switch (validation) {
        case validation:
          return themes.colors[validation];
        default:
          break;
      }
    }};

    outline-color: ${({ validation }) => {
      switch (validation) {
        case validation:
          return themes.colors[validation];
        default:
          break;
      }
    }};
  }
`;

export default styled.label`
  display: flex;
  align-items: baseline;

  font-size: 16px;
  font-weight: 500;

  & pre {
    font-size: 10px;
  }
  // &::first-letter { text-transform: uppercase; }
`;

export const ErrorMessage = styled(FormikError)`
  font-size: 14px;
  font-weight: 400;

  color: ${themes.colors.error};
`;

export const Div = styled.div`
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;

  & h2 {
    margin: 0;

    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
  }
`;
